.techContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-bottom: 30px;
}

.techHeader, h4{
  /* font-weight: normal; */
}

.techText{
  margin-top: 25px;
  margin-bottom: 50px;
}

.allCases{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.casesContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.case{
  width: 100%;
  height: 30%;
  margin-bottom: 5%;
}

.case img{
  width: 100%;
}

/* Media queries */

/* Desktop */
@media (min-width: 900px){
  .allCases{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }
  .casesContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .case{
    width: 30%;
    height: 30%;
    margin-bottom: 2%;
  }
}

/* tablet */
@media (min-width: 600px){
  .case{
    width: 30%;
    height: 30%;
    margin-bottom: 2%;
  }

  .casesContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}



